import { getCategory, getCategoryDetail} from 'api/api'
import Scroll from 'base/scroll/scroll'
import Tarbar from 'base/tarbar/tarbartwo'
import { mapMutations, mapGetters } from 'vuex'
export default {
	name: 'category',
	data () {
		return {
			categoryList: [],
			goods: [],
			search_placeholder: '',
			currentIndex: 0
		}
	},
	created () {
		this._getCategory()
	},
	computed: {
		
	},
	methods: {
		_getCategory () {
			getCategory().then((res) => {
				this.categoryList = res.data
				this.search_placeholder = res.search_placeholder
				return getCategoryDetail(res.data[0].id)
			}).then((res) => {
				this.goods = res.data
			})
		},
		selectMenu (index,id) {
			this.currentIndex = index
			getCategory().then((res) => {
				this.categoryList = res.data
				this.search_placeholder = res.search_placeholder
				return getCategoryDetail(id)
			}).then((res) => {
				this.goods = res.data
			})
		},
		toDetail (item) {
			// console.log(item)
			this.setCateDetail(item)
			// this.
			this.$router.push({
				path: '/category-detail'
			})
		},
		...mapMutations({
      setCateDetail: 'SET_CATEDETAIL'
	  })
	},
	components : {
		Tarbar,
		Scroll
	}
}



